/* image grid styles */
.img-grid{
  margin: 20px auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 40px;
}
.img-wrap{
  overflow: hidden;
  height: 0;
  padding: 50% 0;
  /* padding controls height, will always be perfectly square regardless of width */
  position: relative;
  opacity: 1;
}
.img-wrap img{
  min-width: 0%;
  min-height: 0%;
  max-width: 100%;
  position: absolute;
  top: 0;
  left:0;
  width: 90%;
}

